.postsContainer {
  position: relative;
  padding: 20px;
}

.postsHeading {
  text-align: center;
  font-size: 2.5rem;
  color: var(--primary-color-blue);
  opacity: 0.8;
  margin-bottom: 30px;
  text-shadow: 2px 2px 4px rgba(24, 141, 230, 0.3);
}

.postList {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
}

.backgroundShapes {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  pointer-events: none;
}

.backgroundShapes::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 200px;
  height: 200px;
  background-color: var(--primary-color-blue);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  animation: float 5s infinite alternate;
  opacity: 0.8;
}